import React from 'react'

class PersonStatus extends React.Component {
  state = {
    editMode: false,
    status: this.props.status
  }

  activateEditMode = () => {
    this.setState({
      editMode: true
    })
  }

  deactivateEditMode = () => {
    this.setState({
      editMode: false
    })
    this.props.updateStatus(this.state.status)
  }

  onStatusChange = (e) => {
    this.setState({
      status: e.currentTarget.value
    })
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.status !== this.props.status) {
      this.setState({
        status: this.props.status
      })
    }
  }

  render() {
    return (
      <div>
        {this.state.editMode
          ? <input type="text" onChange={this.onStatusChange} onBlur={this.deactivateEditMode} value={this.state.status}
                   autoFocus/>
          : <p onDoubleClick={this.activateEditMode}>{this.props.status || '---'}</p>}
      </div>
    )
  }
}

export default PersonStatus